.button {
  border-radius: 3px;
width: auto;
  height: 30px;
  padding: 5px 24px 5px 24px;
  background: rgb(0, 77, 153);
  color: rgb(255, 255, 255);
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 600;
  border-radius: 3px;
  
}

.disabled {
  background: rgb(0, 77, 153);
  opacity: 0.5;
}

.secondary {
  /* width: 176px; */
  border-radius: 3px;
  color: rgb(0, 102, 204);
  border: 1px solid rgb(0, 102, 204);
  background: rgb(255, 255, 255);
  padding: 5px 24px 5px 24px;
}

.default {
  background: rgb(0, 102, 204);
  color: rgb(255, 255, 255);
  padding: 5px 24px 5px 24px;
}

.red {
  background: rgb(204, 20, 57);
  color: rgb(255, 255, 255);
}

.default:hover {
  background: rgb(0, 128, 255);
  /* width: 167px; */
}

.red:hover {
  background: rgb(255, 26, 71);
  /* width: 167px; */
}

.secondary:hover {
  /* width: 167px; */
  background: rgb(229, 240, 250);
}

.default:active {
  background: rgb(0, 89, 179);
}

.secondary:active {
  /* background: rgb(201, 229, 255); */
  background:rgb(0, 89, 179);
  color:rgb(229, 240, 250);
}
.secondary:clicked{
  background:rgb(0, 89, 179);
  color:rgb(229, 240, 250);
}

.red:active {
  background: rgb(179, 18, 50);
}

.button:disabled {
  background: rgb(0, 77, 153);
  /* width: 133px; */
}
